import css from './layout.css';
import PropTypes from 'prop-types';

const Layout = ({ children }) => (
  <section className={css.layout}>
    <h1>Main Title</h1>
    {children}
  </section>
);

Layout.propTypes = {
  children: PropTypes.node.isRequired,
};

export default Layout;


